// /* ---------------hero --------------- */

.header {
  position: relative;
  background-color: rgba(255, 184, 202, 1);
  overflow: hidden;
  min-height: 502px;
  margin: 0 auto;
}

@media screen and (max-width: 767px) {
  .header {
    max-width: 480px;
    min-height: 502px;
  }
}

@media screen and (min-width: 768px) {
  .header {
    width: 768px;
    min-height: 416px;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    width: 1280px;
    min-height: 664px;
  }
}

.header__main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 8px 0;

  @media screen and (min-width: 768px) {
    margin-top: 32px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 37px;
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 0;
  }
}

.nav {
  @media screen and (max-width: 1279px) {
    display: none;
  }
}

.nav {
  position: absolute;
  left: 390px;
  z-index: 2;
}

.site-nav {
  display: flex;
  align-items: center;
  margin-inline-start: auto;
}

.site-nav__item + .site-nav__item {
  margin-left: 35px;
}

.site-nav__link {
  display: block;
  padding-top: 40px;
  padding-bottom: 40px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.31;
  letter-spacing: 0.02em;
  text-decoration: none;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  color: #ffffff;

  &:hover,
  &:focus {
    color: #d41443;
  }
}

.site-nav__link--current {
  color: #d41443;
}
// --mobile__menu--

.mobile-menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
  width: 250px;
  height: 100%;
  padding: 0;
  padding-top: 60px;
  background-color: #e17992;
  z-index: 10;

  transform: translateX(100%);
  transition: transform 250ms ease-in-out;

  @media screen and (max-width: 479px) {
    max-width: 479px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
    width: 33.3%;
    height: 100vh;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.mobile-menu.is-open {
  transform: translateX(0%);
  .mobile-menu__link--current {
    color: #d41443;
  }
}

.mobile-menu__list {
  margin: 0 auto;
  margin-bottom: 20px;
}

.mobile-menu__btn {
  align-items: center;
  justify-content: center;

  min-width: 186px;
  height: 44px;
  border: none;
  border-radius: 22px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;

  background-color: #ffffff;
  color: #d41443;

  cursor: pointer;
}
.mobile-menu__item {
  width: 100%;
  border-bottom: 1px solid #e18298;
  padding: 13px 20px;
}

.mobile-menu__link {
  font-family: 'DM Sans';
  font-weight: 700;

  line-height: 1.29;
  letter-spacing: 0.02em;
  text-decoration: none;
  color: #ffffff;

  &:not(:last-child) {
    margin-bottom: 32px;
  }
}

.header__mobile-menu-btn {
  display: flex;
  padding: 0;

  align-items: center;
  justify-content: center;
  width: 20px;
  height: 12px;
  margin-left: auto;
  margin-right: 0;
  border: none;
  background-color: transparent;
  z-index: 10;
  cursor: pointer;

  &:hover > .mobile-menu__icon,
  &:focus > .mobile-menu__icon {
    fill: #d41443;
  }

  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.header__mobile-menu-icon {
  transition: fill 2500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.header__mobile-menu-btn.is-open .icon__open {
  opacity: 0;
}

.header__mobile-menu-btn .icon__open {
  opacity: 1;
}

.header__mobile-menu-btn .icon__close {
  opacity: 0;
}

.header__mobile-menu-btn.is-open .icon__close {
  opacity: 1;
}

.btn__header {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  min-width: 159px;
  height: 44px;
  border: none;
  border-radius: 22px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;

  background-color: #ffffff;
  color: #d41443;

  cursor: pointer;

  margin-left: 32px;

  @media screen and (max-width: 767px) {
    display: none;
  }

  &.check {
    @media screen and (max-width: 1200px) {
      margin: 0 auto;
    }
  }
}

.btn__icon {
  margin-left: 12px;
}

.nav {
  @media screen and (max-width: 1279px) {
    display: none;
  }
}

.nav {
  position: absolute;
  left: 390px;
  z-index: 2;
}

.site-nav {
  display: flex;
  align-items: center;
  margin-inline-start: auto;
}

.site-nav__item + .site-nav__item {
  margin-left: 35px;
}

.site-nav__link {
  display: block;
  padding-top: 40px;
  padding-bottom: 40px;

  font-weight: 500;
  font-size: 16px;
  line-height: 1.31;
  letter-spacing: 0.02em;
  text-decoration: none;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  color: #ffffff;

  &:hover,
  &:focus {
    color: #d41443;
  }
}

.site-nav__link--current {
  color: #d41443;
}

